import { useEffect, useState } from "react";
import "./AdminTasks.css";

const AdminTasks = () => {
    const [ users, setUsers ] = useState([]);
    const [ tests, setTests ] = useState([]);

    const [ showUsers, setShowUsers ] = useState([]);
    const [ showTests, setShowTests ] = useState([]); 

    const [ curFIO, setCurFIO ] = useState("");
    const [ curTEST, setCurTEST ] = useState("");
    const [ curSTART, setCurSTART ] = useState("");
    const [ curEND, setCurEND ] = useState("");  

    useEffect(()=> {
        setUsers([]);
        fetch(`https://cab07.000webhostapp.com/sql_Users.php`)
        .then(res => res.json())
        .then(
          (result) => {            
            setUsers(result.users);                     
        });
        setTests([]);
        fetch(`https://cab07.000webhostapp.com/sql_Tests.php`)
        .then(res => res.json())
        .then(
          (result) => {            
            setTests(result.tests);                     
        });
    },[]);
    useEffect(()=>{
        let showUs = [];
        users.forEach( el => {
            showUs.push(
                <option>{el.user_fio}</option>
            )
        });
        setShowUsers( showUs );
    },[users]);
    useEffect(()=>{
        let showTes = [];
        showTes.push(<option></option>)
        tests.forEach( el => {
            showTes.push(
                <option>{el.test_name}</option>
            )
        });
        setShowTests( showTes );
    },[tests]);
    let ClickHandler = () => {
        fetch(`https://cab07.000webhostapp.com/sql_addTasks.php?
                USER_FIO="`+curFIO+
                `"&TEST_NAME="`+curTEST+
                `"&START_DATE_TIME=`+curSTART+
                `&END_DATE_TIME=`+curEND)
        .then(res => res.json())
        .then(
          (result) => {           
                console.log ( result )           
        });
    };
    return (
        <div className = "AdminTasks">
            AT
            <p
                className = "AdminTask_head">Задаць тэст</p>
            <table
                className = "AdminTask_tab">
                <thead>
                    <tr>
                        <td>Вучань</td>
                        <td>Тэст</td>
                        <td>Пачатак</td>
                        <td>Канец</td>
                        <td></td>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>
                            <select
                                onChange = { (e) => { setCurFIO (e.target.value ) } }
                                placeholder = "Выбраць вучня">
                               { showUsers }
                            </select>
                        </td>
                        <td>
                            <select
                                onChange = { (e) => { setCurTEST (e.target.value ) } }
                                placeholder = "Выбраць тэст">
                                    {showTests}
                            </select> 
                        </td>
                        <td>
                            <input 
                                onSelect = { (e) => { setCurSTART (e.target.value ) } }
                                type="datetime-local"               
                                placeholder = "Пачатак выканання"></input>
                        </td>
                        <td>
                            <input 
                                onSelect = { (e) => { setCurEND (e.target.value ) } }
                                type="datetime-local"               
                                placeholder = "Канец выканання"></input>
                        </td>
                        <td>
                            <button
                                onClick = { ClickHandler }>
                                Задаць!
                            </button>
                        </td>
                    </tr>
                </tbody>
            </table>
           
        </div>
    )
}
export default AdminTasks;

