import { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import './TestTemplate.css';

const TestTemplateB_0 = ( prop ) => {
    const dispatch = useDispatch();
    const [curAnwser, setCurAnswer ] = useState("");
    useEffect (() => {
        setCurAnswer("");
    },[prop.q])
    return (
        <div className = "TestTemplateB_0">
            <p 
                className = "TTB0_quest">
                    <b>{prop.q}</b>
                </p>
            <p 
                className = "TTB0_quest_text">
                    {prop.t}
                </p>
            <input
                type = "text"
                className = "TTB0_ans"
                value = {curAnwser}
                onChange = { (e) => {                     
                    setCurAnswer(e.target.value);
                    dispatch ({
                        type: "SEND_USER_TEST_ANSWER",
                        answer : e.target.value.toUpperCase()
                    })
                }}>
                </input>
        </div>
    )
}

export default TestTemplateB_0;