import { useEffect, useState } from 'react';
import { useDispatch, useStore } from 'react-redux';

import './TestsMenu.css';

const TestsMenu = () => {
    const dispatch = useDispatch();
    const testsList = useStore();

    const [ displayTestsList, setDisplayTestsList ] = useState();

    let TestsMenu_clickHandler = ( tt ) => {
        dispatch({
            type : "SWITCH_TO_TEST", 
            selectedTest : tt 
        });
    }
    useEffect ( ()=> {
        let qq = testsList.getState();
        let arr = [];
        let i = 0;
        qq.STORE_testsSets.forEach(element => {
            arr.push( 
                <li
                    key = {"testsListEl"+i}
                    onClick = { () => TestsMenu_clickHandler( element )}>
                    {element.testSet_Name}
                </li>
            );
            i += 1;
        });
        setDisplayTestsList( arr );
    },[ testsList]);    
    return (
        <div className = "TestsMenu">
            <ul
                className = "TM_menu">
                { displayTestsList }
            </ul>
        </div>
    )
}

export default TestsMenu;