import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import './TestTemplate.css';

let TestTemplateA = ( prop ) => {
    const curAnwser = useSelector( state => state.STORE_curTestAnswer)
    
    const [ch1, setCh1] = useState("");
    const [ch2, setCh2] = useState("");
    const [ch3, setCh3] = useState("");
    const [ch4, setCh4] = useState("");
    const [ch5, setCh5] = useState("");

    const dispatch = useDispatch();

    useEffect( () => {
        if (curAnwser === "") {
                setCh1("");
                setCh2("");
                setCh3("");
                setCh4("");
                setCh5("");
        }        
    },[curAnwser])
    useEffect (() => {
        dispatch({
           type:  "SEND_USER_TEST_ANSWER",           
           answer : ch1+ch2+ch3+ch4+ch5
        });
    },[ch1, ch2, ch3, ch4, ch5])

    return (
        <div className = "TestTemplateA">            
            <p
                className = "TTA_quest">
                {prop.q}</p>      
            <ul
                className = "TTA_list">                 
               <li> 
                    {/* ans0 */}
                    <input 
                        type = "checkbox"
                        className = "TTA_ans"
                        name ="TTA_ans0"
                        checked = {ch1 !== ""}
                        onChange = { () => {ch1 === "1"?setCh1(""):setCh1("1")}}></input>
                    <label                        
                        htmlFor = "TTA_ans0">{prop.a[0]}</label>
                </li>
                <li>
                    {/* ans1 */}
                    <input 
                        type = "checkbox"
                        className = "TTA_ans"
                        name ="TTA_ans1"
                        checked = {ch2 !== ""}
                        onChange = { () => {ch2 === "2"?setCh2(""):setCh2("2")}}></input>
                    <label
                        htmlFor = "TTA_ans1">{prop.a[1]}</label>
                </li>
                <li>
                    {/* ans2 */}
                    <input 
                        type = "checkbox"
                        className = "TTA_ans"
                        name ="TTA_ans2"
                        checked = {ch3 !== ""}
                        onChange = { () => {ch3 === "3"?setCh3(""):setCh3("3")}}></input>
                    <label
                        htmlFor = "TTA_ans2">{prop.a[2]}</label>
                </li>
                <li>
                    {/* ans3 */}                
                    <input 
                        type = "checkbox"
                        className = "TTA_ans"
                        name ="TTA_ans3"
                        checked = {ch4 !==""}
                        onChange = { () => {ch4 === "4"?setCh4(""):setCh4("4")}}></input>
                    <label
                        htmlFor = "TTA_ans3">{prop.a[3]}</label>
                </li>
                <li>
                    {/* ans4 */}
                    <input 
                        type = "checkbox"
                        className = "TTA_ans"
                        name ="TTA_ans4"
                        checked = {ch5 !== ""}
                        onChange = { () => {ch5 === "5"?setCh5(""):setCh5("5")}}></input>
                    <label
                        htmlFor = "TTA_ans4">{prop.a[4]}</label>
                </li>              
                
            </ul>
        </div>
    )
}

export default TestTemplateA;