import SubMainMenu from '../SubPages/SubMainMenu/SubMainMenu';
import TestsMenu from '../SubPages/TestsMenu/TestsMenu';
import TestsPage from '../SubPages/TestsPage/TestsPage';
import Cabinet from '../SubPages/Cabinet/Cabinet';
import AdminPanel from '../SubPages/AdminPanel/AdminPanel'
import AdminTasks from '../SubPages/AdminTasks/AdminTasks';

import React, { useState, useEffect } from 'react';

import './MainPage.css';
import { useSelector, useDispatch } from 'react-redux';

const MainPage = () => {    
    const dispatch = useDispatch();   
    const curSubPage = useSelector( state => state.STORE_curSubPage);  
    const curUserId = useSelector( state => state.curUserId ); 
    const curUserFio = useSelector( state => state.curUserFIO); 
    const [ showCabBut, setCabBut ] = useState('');
    const [ showTaskBut, setTaskBut ] = useState('');
    const [ curShowSubPage, setCurShowSubPage ] = useState('');

    let MainNav_clickHandler = ( tt ) => {
        dispatch({
            type : "SWITCH_TO_NAV_MENU_EL", 
            targetPage : tt 
        });
    }
     useEffect (()=> {                   
        switch ( curSubPage ) {
            case "sMainMenu" : 
                setCurShowSubPage( <SubMainMenu />);                
                break;
            case "sTestsMenu" : 
                setCurShowSubPage( <TestsMenu />);
                break;
            case "sTest" : 
                setCurShowSubPage( <TestsPage />);
                break;
            case "sCabinet" : 
                setCurShowSubPage( <Cabinet />);
                break;
            case "sAdminPanel" : 
                setCurShowSubPage( <AdminPanel />);
                break;
            case "sAdminTasks" : 
                setCurShowSubPage( <AdminTasks/>);
                break;
            default :
                setCurShowSubPage( <SubMainMenu />)
        };        
    },[curSubPage])
    useEffect ( () => {       
        if (curUserId == 0 ) {
            setCabBut(
                <button
                    className = "MP_nav_but"
                    onClick = { () => { MainNav_clickHandler("sAdminPanel")} }
                    >{"Вынікі тэстаў"}
                </button>
            );
            setTaskBut(
                <button
                    className = "MP_nav_but"
                    onClick = { () => { MainNav_clickHandler("sAdminTasks")} }
                    >{"Задаць тэст"}
                </button>
            );
        }
        else {
            setCabBut(
                <button
                    className = "MP_nav_but"
                    onClick = { () => {MainNav_clickHandler("sCabinet")} } //to Personal cabinet                        
                    >
                        {"Кабінет: " + curUserFio }
                </button>   
            )
        }                
    },[curUserId])
    return (
        <div className = "MainPage">             
            <div            
                className = "MP_menu">                 
                    <button
                        className = "MP_nav_but"
                         onClick = { () => MainNav_clickHandler("sTestsMenu")}                     >          
                    ТЭСТЫ</button>
                    { showCabBut }
                    { showTaskBut }
                </div>
           { curShowSubPage }
        </div>
    )
}

export default MainPage;