import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import "./TestTemplate.css";

const TestTemplateB_2 = ( props ) => {
    const dispatch = useDispatch();
    const [curAnwser, setCurAnswer] = useState("");
    useEffect (() => {
        setCurAnswer("");
    },[props.q])
    return (
        <div className = "TestTemplateB_2">
            <p 
                className = "TTB2_quest"
                > { props.q }</p>
            <div   
                className = "TTB2_LBlock">
                    <p>{props.t[0]}</p>
                    <p>{props.t[1]}</p>
                    <p>{props.t[2]}</p>
                    <p>{props.t[3]}</p>
                    <p>{props.t[4]}</p>
                </div>
            <div 
                className = "TTB2_RBlock">
                    <p>{props.t[5]}</p>
                    <p>{props.t[6]}</p>
                    <p>{props.t[7]}</p>
                    <p>{props.t[8]}</p>
                    <p>{props.t[9]}</p>
                </div>
            <input
                className = "TTB2_input"
                type = "text" 
                value = {curAnwser}    
                onChange = { (e) => { 
                    console.log(e.target.value);
                    setCurAnswer(e.target.value);
                    dispatch ({
                        type: "SEND_USER_TEST_ANSWER",
                        answer : e.target.value.toUpperCase()
                    })
                }}          
                ></input>                
        </div>
    )
}

export default TestTemplateB_2;