import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import TestTemplateA from '../TestsTemplates/TestTemplateA';
import TestTemplateA_1 from '../TestsTemplates/TestTemplateA_1';
import TestTemplateB from '../TestsTemplates/TestTemplateB';
import TestTemplateB_0 from '../TestsTemplates/TestTemplateB_0';
import TestTemplateB_2 from '../TestsTemplates/TestTemplateB_2';

import './TestsPage.css';

const TestsPage = ( ) => {     
    const dispatch = useDispatch();   
    const curUserId = useSelector(state => state.curUserId );
    const curUserFIO = useSelector( state =>state.curUserFIO );

    const curTestSet = useSelector( state =>state.STORE_selectedTestSet);    
    const curUserTestAnswer = useSelector( state => state.STORE_curTestAnswer);
    const [ curTestArr, setCurTestArr ] = useState([]);
    const [ curQuest, setCurQuest ] = useState( );
    const [ curRightArr, setCurRightArr ] = useState([]); 
    const [ curQuestIndex, setCurQuestIndex ] = useState(0);
    const [ rightCount, incRightCount ] = useState(0);
    const [ userTestData, setUserTestData ] = useState({}); //собираем данне о текущем выполнении теста
        // формат {
        //     dateTime
        //     userId
        //     testID
        //     answers [
        //         "",
        //         ""
        //     ],
        //  result : 
        // }
    
    function clickHandler () {             
        let dat = userTestData;
        dat.curUserAnswers[curQuestIndex] = curUserTestAnswer;           

        if ( curUserTestAnswer === curRightArr[curQuestIndex]) {
            incRightCount (rightCount + 1 );
            alert( "Дакладна!" );
        };
        if ( curQuestIndex < curTestArr.length-1 ) {
            setCurQuestIndex( curQuestIndex + 1); 
        }
        else {
            alert( "Ваш вынік: "+rightCount + ' з '+curTestArr.length + " пытанняў (" + (rightCount/curTestArr.length)*100 + "%)");
            dat.result = rightCount/curTestArr.length*100;       
            let q = dat.curUserAnswers;                     
            fetch(`https://cab07.000webhostapp.com/sql_setTestResult.php?
                user_fio="`+curUserFIO+`"&
                test_name="`+dat.curTestName+`"&
                date_time="`+ Date(dat.dateTime)+`"&
                user_result=`+dat.result+`&
                user_answers="`+q.join("@")+`"`)
            .then(res => res.json())
            .then(
            (result) => {   
                      
            },
            // Примечание: важно обрабатывать ошибки именно здесь, а не в блоке catch(),
            // чтобы не перехватывать исключения из ошибок в самих компонентах.
            (error) => {     
                  
            }
            ) 
            dispatch({
                type : "SWITCH_TO_NAV_MENU_EL",
                targetPage : "sTestsMenu"
            })
        };
        setUserTestData(dat);          
        dispatch({
            type : "SEND_USER_TEST_ANSWER",
            answer: "",
        });    }
    useEffect (() => {
        let dat = userTestData;
        let dt = Date.now();
        dat.dateTime = dt;
        dat.curId = curUserId;
        dat.curTestName = curTestSet.testSet_Name;
        dat.curUserAnswers = [];
        setUserTestData(dat);
    },[])
    useEffect ( () =>{        
        let arr = [];
        let rarr = [];            
        curTestSet.testSet_quests.forEach(element => {    
            switch ( element.TYPE ) {
                case "B" :
                    arr.push(<TestTemplateB q = {element.QUEST}/>);                    
                    break;
                case "A" : 
                    arr.push(<TestTemplateA q = {element.QUEST} a = {element.ANSWERS}/>);
                    break;
                case "B_0" : 
                    arr.push(<TestTemplateB_0 q = {element.QUEST} t = {element.QUEST_TEXT}/>);
                    break;
                case "A_1" : 
                    arr.push(<TestTemplateA_1 q = {element.QUEST} t = {element.QUEST_TEXT} a = {element.ANSWERS}/>);
                    break;
                case "B_2" : 
                    arr.push(<TestTemplateB_2 q = {element.QUEST} t = {element.QUEST_TEXT} />);
                    break;
                default : ;
            } ;      
            rarr.push(element.RIGHT);      
        });
        setCurTestArr( arr );
        setCurQuest( arr[curQuestIndex]);
        setCurRightArr( rarr); 
    }, [curTestSet,curQuestIndex])
    return (
        <div className = "subTests">           
            { curQuest }
           <button
                className = "subTest_but"
                onClick = { clickHandler }>
                    Next quest</button>
        </div>
    )
}

export default TestsPage;