import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import "./Cabinet.css";

const Cabinet = () => {
    const [ baseTasks, setBaseTasks ] = useState([]);
    const [ showTasks, setShowTasks ] = useState([]);

    const [ baseUserResults, setBaseUsersResults ] = useState([]);
    const [ showUserResults, setShowUserResults ] = useState([]);

    const curUserFio = useSelector( state => state.curUserFIO);
  
    useEffect (()=>{       
        setBaseUsersResults([]);
        setBaseTasks([]);
        fetch(`https://cab07.000webhostapp.com/sql_getTestsResults.php`)
        .then(res => res.json())
        .then(
          (result) => {            
            setBaseUsersResults(result.results);                     
        });
        fetch(`https://cab07.000webhostapp.com/sql_getTasks.php`)
        .then(res => res.json())
        .then(
          (result) => {            
            setBaseTasks(result.tasks);                     
        });
    },[])   
    useEffect (() => {
        setShowUserResults([]);          
            let shUserRes = [];   
            baseUserResults.forEach( el => {     
                if( el.user_fio === curUserFio ) {                          
                    shUserRes.push(
                        <tr key = {1+el.dateTime}>
                            <td>{el.test_name}</td>
                            <td>{el.result}</td>
                            <td>{ el.dateTime.substr(0, 25) } </td>
                        </tr> 
                    );  
                }                      
            });        
            setShowUserResults( shUserRes );           
    },[baseUserResults]);
    useEffect (() => {
        setShowTasks([]);          
            let shUserTasks = [];   
            baseTasks.forEach( el => {     
                if( el.task_user_fio === curUserFio ) {                          
                    shUserTasks.push(
                        <tr key = {1+el.task_start_time}>
                            <td><button                                      
                                    className = "Cab_task_but">
                                        {el.task_test_name}
                                  </button>
                            </td>
                            <td>{el.task_start_time}</td>
                            <td>{ el.task_end_time } </td>
                        </tr> 
                    );  
                }                      
            });                    
            setShowTasks( shUserTasks );           
    },[baseTasks])
    return (
        <div 
            className = "Cabinet">
                <table
                    className = "Cab_task">
                        <caption>Заданні:</caption>
                    <thead>
                        <tr>                            
                            <td>TaskLink</td>
                            <td>Start datetime</td>
                            <td>End datetime</td>
                        </tr>
                    </thead>
                    <tbody>
                            { showTasks }
                    </tbody>
                </table>
                <table
                    className = "Cab_res">
                        <caption>Вынікі тэстаў:</caption>
                    <thead>
                        <tr>
                            <td>Test name </td>
                            <td> Test result</td>
                            <td> test datetime</td>
                        </tr>
                    </thead>
                    <tbody>
                        { showUserResults }
                    </tbody>    
                </table>
        </div>
    )
}

export default Cabinet;