import initState from './initState';

let initRed = ( state = initState, action ) => {
    switch ( action.type ) {
        case "LOG_IN" : 
            return {
                ...state,
                curPage : "MainPage",
                curUserId : action.baseId,  
                curUserFIO : action.baseFIO,                      
                curUserAccLvl : action.baseAccLvl,
                curUserSchool : action.baseSchool,
                curUserClass : action.baseClass
            };           
        case "SWITCH_TO_NAV_MENU_EL" :          
            return {
                ...state,
                STORE_curSubPage : action.targetPage
            };           
        case "SWITCH_TO_TEST" : 
            return {
                ...state,
                STORE_curSubPage : "sTest",
                STORE_selectedTestSet : action.selectedTest
            };  
        case "SEND_USER_TEST_ANSWER" :            
            return {
                ...state,
                STORE_curTestAnswer : action.answer
            }                 
        default:
            return state;           
    }
}

export default initRed;