import { useState } from 'react';
import { useDispatch } from 'react-redux';
import './TestTemplate.css';

const TestTemplateB = ( prop ) => {
    const dispatch = useDispatch();
    const [ curInputValue, setCurinputValue ] = useState("");

    let inputChangeHandler = ( e ) => {
        setCurinputValue ( e.target.value );
        alert(curInputValue);
    }
   
    return (
        <div className = "TestTemplateB">
            <p 
                className = "TTB_quest">
                   <b> {prop.q} </b>
                </p>
            <input
                type = "text"
                className = "TTB_ans">
                value = { curInputValue }
                onChange = {  (e) => inputChangeHandler() }
                </input>
        </div>
    )
}

export default TestTemplateB;