import "./LoginPage.css";

import { useDispatch } from 'react-redux'; 
import { useEffect, useState } from "react";

const LoginPage = () => {
    const dispatch = useDispatch();  

    const [ login, setLogin ] = useState('');
    const [ pass , setPass  ] = useState('');
    const [ id, setId ] = useState();

    const LP_but_handler = () => {      
       accToBase();
    };
    let accToBase = () => {
        fetch(`https://cab07.000webhostapp.com/sql_login.php?login=`+login+`&pass=`+pass+``)
        .then(res => res.json())
        .then(
          (result) => {
              if ( result.userId != -1 ) {
                dispatch({
                    type : "LOG_IN",
                    baseId : result.userId,
                    baseFIO : result.userFIO,
                    baseAccAccLvl : result.userAccLvl,
                    baseSchool : result.userSchool,
                    baseClass : result.userClass
                })
              }              
              else {
                  alert("Login pass error " + login + "___"+pass)
              }
          },
          // Примечание: важно обрабатывать ошибки именно здесь, а не в блоке catch(),
          // чтобы не перехватывать исключения из ошибок в самих компонентах.
          (error) => {            
          }
        )
    }
    return (
        <div className = "LoginPage">    
            <p
                className = "LP_header">Welcome</p>      
            <div 
                className = "LP_logo"></div>  
            <input 
                type = "text"
                className = "LP_login"
                value = { login }
                //placeholder = "Логін"
                onChange = { (e) => { setLogin(e.target.value)} }
                required ></input>
            <input 
                type = "password"
                className = "LP_pass"
                value = { pass }
                //placeholder = "Пароль"
                onChange = { (e) => { setPass (e.target.value ) }}></input>
            <button
                className = "LP_but"
                onClick = {LP_but_handler}>Войти в систему</button>
        </div>
    )
}

export default LoginPage;